<template>
	<div class="kugou-content">
		<!-- banner图 -->
		<img src="../assets/images/1.jpg" alt class="bg-banner" />
		<!-- 滚动号码 -->
		<div class="kugou-scroll">
			<ul class="bg-scroll">
				<img src="../assets/images/name.png" alt class="notice-img" />
				<li v-for="(item, i) in list" :key="i" ref="rollLi" :class="{ 'anim' : animate && i == 0 }">
					<span class="lkq-name">{{ item.phone }}</span>
				</li>
			</ul>
		</div>
		<!-- 抽奖弹窗 -->
		<div class="turntable">
      <!-- 转动背景 -->
			<img src="../assets/images/flash.png" alt :class="['run-item-' + current]" />
			<!-- 礼品框 -->
			<img src="../assets/images/4.png" alt class="present-content" />
			<!-- 抽奖按钮 -->
			<img
				@click="handleStart"
				src="../assets/images/bigBottom.png"
				alt
				class="start-btn-big"
			/>
		</div>

		<transition name="confirm-fade">
				<div v-if="isShowConfirm" class="my-confirm " @click.stop="clickFun('clickCancel')">
				<div class="confirm-content-wrap confirm1" @click.stop>
					<h3 class="my-confirm-title">{{ titleText }}</h3>
					<img style="width:120px;height: 70px;position: absolute;top: 65px;
    left: 43px;" :src="imagesp" />
					
					<div class="my-operation" style="margin-top: 130px; " >
					
					<div class="confirm-btn" style="bottom:0;color: red;background-color:#F8E8D4;border-radius: 20px;
    width: 200px;
    margin: 0 auto;
    text-align: center;" @click="clickFun('clickConfirm')">
						<p class="my-btn-text" style="font-size:24px;">{{ confirmText }}</p>
					</div>
					</div>
				</div>
				</div>
  		</transition>


		<transition name="confirm-fade">
				<div v-if="isShowConfirm2" class="my-confirm " @click.stop="clickFun('clickCancel')">
				<div style="width:320px;height:340px" class="confirm-content-wrap confirm1" @click.stop>
					
					<h3 class="my-confirm-title">群号：714370774</h3>
					<img style="width:180px;height: 180px;position: absolute;top: 65px;left: 70px;" src="../assets/images/erw.jpg" />
					
					<p></p>
					<div style="margin: 0 auto;font-size:12px;color: #fff;margin-top: 4.1rem;width:320px;text-align: center;">
						<p >扫一扫二维码，加入群聊 </p>
					</div>
					<div style="margin: 0 auto;font-size:12px;color: #fff;margin-top:20px;width:320px;text-align: center;">
						<p >截图或保存上方二维码，加QQ群找相关客服人员领取</p>
					</div>
					
					<div class="my-operation" style="margin-top: 130px; " >
					
					<div class="confirm-btn" style="bottom:0;color: red;background-color:#F8E8D4;border-radius: 20px;
    width: 200px;
    margin: 0 auto;
    text-align: center;" @click="clickFun('clickConfirm')">
						
					</div>
					</div>
				</div>
				</div>
  		</transition>

		  <!-- <transition name="confirm-fade">
				<div v-if="isShowConfirm2" class="my-confirm" style="background-color: #f5f7f9;">
					<div style="margin：0 atuo;text-align: center; ">群号：1223652652</div>
					<div><img style="width：150px;height:80px;" src="../assets/images/erw.jpg" /></div>
					<div style="margin：0 atuo;text-align: center; ">扫一扫二维码，加入群聊。</div>
				</div>
  		</transition> -->
    </div>
	</div>
</template>
<script>
import { Toast } from 'vant';
// let imsrc = require('/src/assets/images/18.png')

import img from '../assets/images/18.png'
export default {
	name: "index",
	data() {
		return {
			animate: false,
		imagesp:'',
			isShowConfirm: false, // 用于控制整个窗口的显示/隐藏
			isShowConfirm2:false,
      titleText: '恭喜获得', // 提示框标题
      confirmText: '点击领取', // 确认按钮的文字
      type: 'confirm', // 表明弹框的类型：confirm - 确认弹窗（有取消按钮）；alert - 通知弹框（没有取消按钮）
      outerData: null, // 用于记录外部传进来的数据，也可以给外部监听userBehavior，事件的函数提供判断到底是哪个事件触发的
			show1:false,
            default: false,
			initSpeed: 200,  // 初始速度
			speed: null,  // 变化速度
			diff: 20,  // 速度变化的值，值越大，变化地越快
			award: {},  // 抽中的奖品
			awards: [  // 奖品列表
        {
          id: 1,
          name: require("../assets/images/11.png")
        },
        {
          id: 2,
         name: require("../assets/images/12.png")
        },
        {
          id: 3,
         name: require("../assets/images/13.png")
        },
        {
          id: 4,
         name: require("../assets/images/14.png")
        },
        {
          id: 5,
         name: require("../assets/images/15.png")
        },
        {
          id: 6,
         name: require("../assets/images/16.png")
        },
        {
          id: 7,
          name: require("../assets/images/17.png")
        },
        {
          id: 8,
          name: require("../assets/images/18.png")
        }
			],
			time: 0,  // 记录开始抽奖的时间
			current: 0,  // 当前转动的位置
			isRuningLucky: false,  // 是否正在抽奖
			list: [  // 中奖号码
				{
					phone: "186****2336抽中多皮肤"
				},
				{
					phone: "166****2336抽中多皮肤"
				},
				{
					phone: "156****2336抽中多皮肤"
				}
			]
		};
	},
	created() {
	},
	mounted() {
		let scrollTimer = setInterval(this.scroll, 2000);
	},
	methods: {
		// 抽奖
		handleStart() {
			if (this.isRuningLucky) {
				
			}
			if (isNaN(Number(this.initSpeed))) {
				return false;
			}
			this.speed = this.initSpeed;
			// 开始抽奖
			this.isRuningLucky = true;
			this.time = Date.now();
			this.drawAward();
			
		},
		drawAward() {
			// 请求接口，模拟一个抽奖数据(假设请求时间为2s)
			setTimeout(() => {
				this.award = {
					id: "4"
				};
				console.log("返回的抽奖结果是", this.award);
			}, 2000);
			this.move();
		},
		move() {
			let timer = setTimeout(() => {
				this.current++;
				if (this.current > 7) {
					this.current = 0;
				}
				// 若抽中的奖品id存在，则开始减速转动
				if (this.award.id && (Date.now() - this.time) / 1000 > 2) {
					
          this.speed += this.diff; // 转动减速
          
					// 若转动时间超过4秒，并且奖品id等于小格子的奖品id，则停下来
					if (
						(Date.now() - this.time) / 100 > 4 &&
						this.award.id == this.awards[this.current].id,
						this.imagesp=this.awards[this.current].name
						
					) {
						clearTimeout(timer);
						this.isShowConfirm=true;
						this.imagesp=this.awards[this.current].name
						console.log(this.imagesp)
						
						setTimeout(() => {
							  this.isRuningLucky = false;
								this.show1=true;
								
							// 这里写停下来要执行的操作（弹出奖品框）
							
							
							
						// 
							
						}, 400);
						return;
					}

				// 若抽中的奖品不存在，则加速转动
				} else {
					if (this.speed >= 50) {
						this.speed -= this.diff; // 转动加速
					}
				}

				this.move();
			}, this.speed);
		},
		// 中奖名单滚动
		scroll() {
			this.animate = true;
			let timer = setTimeout(() => {
				this.list.push(this.list[0]);
				this.list.shift();
				this.animate = false;
			}, 500);
		},
		 show (content, config) {
      this.content = content || '<div>ddfdf</div>'

      if (Object.prototype.toString.call(config) === '[object Object]') {
        // 确保用户传递的是一个对象
        this.titleText = config.titleText || '操作提示'
        this.confirmText = config.confirmText || '确认'
        this.type = config.type || 'confirm'
        this.outerData = config.data || null
      }

      this.isShowConfirm = true
    },
    hidden () {
      this.isShowConfirm = false
      this.titleText = '操作提示'
      this.confirmText = '确认'
      this.type = 'confirm'
      this.outerData = null
    },
    clickFun (type) {
      this.isShowConfirm2=true;
      this.hidden()
    }
  }
	
}
</script>
<style lang="stylus" scoped>

.kugou-content {
  background-color: #000;
	width: 100%;
	overflow: hidden;
	position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
	padding-top: 4.1rem;
	// 顶部图片
	.bg-banner {
		position: absolute;
		top: 0;
		right: 0;
		width: 8.4rem;
	}
	// 滚动号码
	.kugou-scroll {
		.bg-scroll {
			position: relative;
			background: url("../assets/images/phone.png") center top no-repeat;
			background-size: 100%;
			width: 100%;
			height: 0.74rem;
			line-height: 0.74rem;
			font-size: 0.35rem;
			overflow: hidden;
			text-align: center;
			letter-spacing: 0.03rem;
		}
		.lkq-name {
			margin: 0 auto;
			color: #fff;
      font-size: 0.28rem;
		}
		.anim {
			transition: all 0.5s linear;
			margin-top: -0.74rem;
		}
	}
	// 抽奖
	.turntable {
		position: relative;
		background: url("../assets/images/lottery.png") center top no-repeat;
		background-size: 8.5rem;
		background-position: -1.06rem -0.9rem;
		height: 8.2rem;
		padding: 1.55rem 0.83rem 1.15rem;
		.present-content {
			width: 75%;
			position: absolute;
			top: 1.68rem;
			left: 0.96rem;
			z-index: 100;
		}
		.start-btn-big {
			width: 1.8rem;
			position: absolute;
			top: 3.55rem;
			left: 2.88rem;
			z-index: 999;
			animation: btn-animation 0.3s ease-out infinite alternate;
		}
		.run-item-0 {
			width: 2.03rem;
		}
		.run-item-1 {
			width: 2.03rem;
			display: block;
			margin: 0 0 0 1.93rem;
		}
		.run-item-2 {
			width: 2.03rem;
			display: block;
			margin: 0 0 0 3.85rem;
		}
		.run-item-3 {
			width: 2.03rem;
			display: block;
			margin: 1.9rem 0 0 3.85rem;
		}
		.run-item-4 {
			width: 2.03rem;
			display: block;
			margin: 3.8rem 0 0 3.85rem;
		}
		.run-item-5 {
			width: 2.03rem;
			display: block;
			margin: 3.8rem 0 0 1.925rem;
		}
		.run-item-6 {
			width: 2.03rem;
			display: block;
			margin: 3.8rem 0 0 0;
		}
		.run-item-7 {
			width: 2.03rem;
			display: block;
			margin: 1.9rem 0 0 0;
		}
	}
	
}

.my-confirm {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    
    z-index:9999;
    /* 这里防止当用户长按屏幕，出现的黑色背景色块，以及 iPhone 横平时字体的缩放问题 */
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }

  /* 进入和出去的动画 */
  .confirm-fade-enter-active {
    animation: opacity 0.3s;
  }
  .confirm-fade-enter-active .confirm-content-wrap {
    animation: scale 0.3s;
  }
  .confirm-fade-leave-active {
    animation: outOpacity 0.2s;
  }

  /* 包裹层容器样式 */
  .confirm-content-wrap {
    position: absolute;
    top: 28%;
    left: 0;
    right: 0;
    width: 220px;
	height 250px;
    margin: 0 auto;
    background-color: #000;
	border:2px #fff solid;
    border-radius: 20px;
    z-index: 999;
    user-select: none;
  }

  /* 顶部标题部分 */
  .my-confirm-title {
    padding-top: 20px;
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    color : #F8E8D4;
  }

  /* 中间内容部分 */
  .my-confirm-content {
    padding: 0 15px;
    padding-top: 20px;
    margin-bottom: 32px;
    text-align: center;
    font-size: 16px;
    color: #666;
    line-height: 1.5;
  }

  /* 底部按钮样式 
  
  .confirm-btn{
	  

  }
  
  /* 其他修饰样式 */
  .my-border-right {
    border-right: 1px solid #eee;
  }

  /* 进来的动画 */
  @keyframes opacity {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes scale {
    0% {
      transform: scale(0);
    }
    60% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
  }

  /* 出去的动画 */
  @keyframes outOpacity {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
@keyframes btn-animation {
	0% {
		transform: scale(1);
	}
	100% {
		transform: scale(0.85);
	}
}

.notice-img {
	position: absolute;
	top: 0.12rem;
	left: 0.55rem;
}
</style>



